import React from 'react';
import Navbar from './Navbar'
import {Link} from 'react-router-dom'

function Stories(props) {
    return (
        <>
        <Navbar/>
        <div className="stories">
          
         <Link>
         <div>Read some inspirational IF stories</div>
         </Link>

        </div>
        </>
    );
}

export default Stories;