import React, { useState, useEffect } from 'react';
import Card from 'react-bootstrap/Card';
import Navbar from './Navbar'
function Bmi(props) {
    const [height, setHeight] =useState(0)
    const [weight, setWeight] = useState(0)
    const [bmi, setBmi] = useState("")
    const [healthMessage, setHealthMessage]=useState("")
    const [showBmiText, setShowBmiText] = useState(false)
    const calculateBmi=()=>{
        setBmi(weight/(height*height))
    }

    const determineHealthiness=()=>{
        if(bmi<18.5){
            setHealthMessage("Your BMI is considered underweight")
        }else if(bmi>18.5 && bmi<25){
            setHealthMessage("Your BMI is considered healthy")
           }else if(bmi>=25){
            setHealthMessage("Your BMI is considered overweight")
        }
}
    useEffect(()=>{
        determineHealthiness()
    }, [bmi])
    return (
        <div className="App">
            <header className="App-header">
            <Navbar/>
            <Card bg="secondary" style={{ width: '80rem', height: '50vh' }}>
        <Card.Body>
          <Card.Title className="title">BMI Calculator</Card.Title>
        <div className="form-group">
        <label>
                Please enter your height in metres 
            </label>
            <input onChange={(e)=>setHeight(e.target.value)} value={height} required/>

        </div>
           <div className="form-group">
           <label>
            Please enter your weight in kilograms 
            </label>
            <input onChange={(e)=>setWeight(e.target.value)} value={weight} required/>

           </div>
            <button className="btn btn-lg btn-success" onClick={()=>{setShowBmiText(true); calculateBmi()}}>Calculate BMI</button>

    <p style={{display:showBmiText ? "block" : "none"}}>Your BMI is {bmi}. {healthMessage}</p>
            </Card.Body>
            </Card>
            </header>
            
        </div>
    );
}

export default Bmi;