import React from 'react';
import Navbar from './Navbar'
import {Link} from 'react-router-dom'

function Explore(props) {
    return (
        <>
        <Navbar/>
        <div className="explore">
            <Link>
            <div>Different IF plans</div>
            </Link>
        </div>
        </>
    );
}

export default Explore;